import { useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import OverlayTrigger from 'react-bootstrap/OverlayTrigger';
import Form from 'react-bootstrap/Form';
import Col from 'react-bootstrap/Col';
import Tooltip from 'react-bootstrap/Tooltip';
import emailjs from '@emailjs/browser';
import { alertMessage } from '~/redux/actions';

import MasterLayout from '~/components/MasterLayout';
import { themeClass } from '../../Theme';
import style from './Contact.module.scss';
import Button from '~/components/Button';
import { IconFonts } from '~/assets/icons';

const cx = require('classnames/bind').bind(style);

function Contact() {
    const iconsContact = [IconFonts['phone'], IconFonts['email']];
    const iconsSocial = [IconFonts['facebook'], IconFonts['github'], IconFonts['linkedin']];
    const theme = useSelector((state) => state.theme);
    const themeMode = themeClass[theme];

    const contactInfo = [
        themeMode.textColor,
        'container',
        'fs-6',
        'text-md-start',
        'text-center',
        'col-xl-3',
        'col-md-5',
        'col-xs-12',
        'mb-5',
    ];
    const iconFollow = [
        themeMode.textDetail,
        'd-flex',
        'justify-content-md-start',
        'justify-content-center',
        'gap-3',
        'fs-5',
        'mb-3',
    ];
    const sendNote = [
        themeMode.textColor,
        'send-note',
        'container',
        'col-xl-9',
        'col-md-7',
        'col-xs-12',
        'order-2',
        'text-md-start',
        'text-center',
        'mb-5',
    ];
    const inputNote = [
        themeMode.secondaryPage.bgColor,
        themeMode.secondaryPage.textColor,
        'border-0',
        'order-1',
        'rounded-0',
        'fs-5',
    ];
    const [validated, setValidated] = useState(false);
    const [values, setValues] = useState({
        name: '',
        email: '',
        message: '',
    });

    const dispactch = useDispatch();
    const handleSubmit = (event) => {
        const form = event.currentTarget;
        if (form.checkValidity() === false) {
                event.stopPropagation();
                event.preventDefault();
        }
        setValidated(true);
        if (form.checkValidity() === true) {
            event.preventDefault();
            emailjs.sendForm('service_0ukqa4z', 'template_plealsf', event.target, 'QPoU1LiMdoE4Spgnq').then(
                (result) => {
                    console.log(result.text);
                    setValues({
                        name: '',
                        email: '',
                        message: '',
                    });
                    dispactch(alertMessage('Your message has been sent successfully!'));
                    setValidated(false);
                },
                (error) => {
                    console.log(error.text);
                },
            );
        }
    };

    return (
        <MasterLayout id="contact" mode={1} titlePage="Contact" subtitlePage="Get in Touch">
            <div className={cx('row', 'd-flex', 'flex-wrap', 'my-5')}>
                <div className={cx(contactInfo)}>
                    <div className={cx('mb-1')}>
                        <h3 className="fs-4">Address</h3>
                        <p className={cx(themeMode.textDetail)}>53 Ấp 1, Xuân Quế, Cẩm Mỹ, Đồng Nai</p>
                    </div>
                    <div className={cx('contact-phone-email', themeMode.textDetail)}>
                        {iconsContact.map((icon, index) => (
                            <p key={index}>
                                <span>{icon.icon}</span>
                                {icon.content}
                            </p>
                        ))}
                    </div>
                    <div className={cx('d-flex', 'flex-column', 'align-items-md-start', 'align-items-center')}>
                        <h3 className="fs-4">Follow me</h3>
                        <div className={cx(iconFollow)}>
                            {iconsSocial.map((icon, index) => (
                                <OverlayTrigger key={index} placement="bottom" overlay={<Tooltip>{icon.name}</Tooltip>}>
                                    {
                                        <a className={cx(themeMode.textDetail)} target="_blank" href={icon.link}>
                                            {icon.icon}
                                        </a>
                                    }
                                </OverlayTrigger>
                            ))}
                        </div>
                    </div>
                </div>
                <div className={cx(sendNote)}>
                    <h3 className="fs-4" style={{ paddingLeft: '16px' }}>
                        Send me a note
                    </h3>
                    <Form
                        noValidate
                        validated={validated}
                        onSubmit={handleSubmit}
                        className="d-flex flex-column align-items-center"
                    >
                        <Form.Group className="mb-4 row g-4 col-12">
                            <Col xl="6" xs="12">
                                <Form.Control
                                    size="lg"
                                    required
                                    type="text"
                                    placeholder="Name"
                                    className={inputNote}
                                    name="name"
                                    value={values.name}
                                    onChange={(e) => setValues({ ...values, name: e.target.value })}
                                />
                                <Form.Control.Feedback type="invalid">Please write your name</Form.Control.Feedback>
                            </Col>
                            <Col xl="6" xs="12">
                                <Form.Control
                                    size="lg"
                                    required
                                    type="email"
                                    placeholder="Email"
                                    className={inputNote}
                                    name="email"
                                    value={values.email}
                                    onChange={(e) => setValues({ ...values, email: e.target.value })}
                                />
                                <Form.Control.Feedback type="invalid">Invalid email</Form.Control.Feedback>
                            </Col>
                            <Col xs="12">
                                <Form.Control
                                    required
                                    size="lg"
                                    as="textarea"
                                    placeholder="Send me messages. . ."
                                    className={inputNote}
                                    rows={5}
                                    name="message"
                                    value={values.message}
                                    onChange={(e) => setValues({ ...values, message: e.target.value })}
                                />
                                <Form.Control.Feedback type="invalid">
                                    Please give me some messages or feedback
                                </Form.Control.Feedback>
                            </Col>
                        </Form.Group>
                        <Button type="submit">Send Message</Button>
                    </Form>
                </div>
                {/* alert message */}
            </div>
        </MasterLayout>
    );
}

export default Contact;
