import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import {
    CDBSidebar,
    CDBSidebarContent,
    CDBSidebarFooter,
    CDBSidebarHeader,
    CDBSidebarMenu,
    CDBSidebarMenuItem,
} from 'cdbreact';
import OverlayTrigger from 'react-bootstrap/OverlayTrigger';
import Tooltip from 'react-bootstrap/Tooltip';

import { publicRoutes } from '~/routes';
import styles from './Sidebar.module.scss';
import { themeClass } from '../../Theme';
import Scrollspy from './useScrollspy';
import { themeToggle } from '../../redux/actions';
import { Moon, Sun2, IconFonts } from '~/assets/icons';

const cx = require('classnames/bind').bind(styles);
const Sidebar = () => {
    const ids = publicRoutes.map((route) => route.path);
    const activeId = Scrollspy(ids, 250);

    const theme = useSelector((state) => state.theme);
    const dispatch = useDispatch();
    const themeMode = themeClass[theme].sideBar;

    const [isChecked, setIsChecked] = useState(theme === 'dark' ? true : false);
    const handleToggle = () => {
        dispatch(themeToggle({ theme: isChecked ? 'light' : 'dark' }));
        setIsChecked(!isChecked);
    };
    const [toggleUp, setToggleUp] = useState(false);
    const handleSidebar = () => {
        setToggleUp(!toggleUp);
    };
    const [toggleWidth, setToggleWidth] = useState(false);
    useEffect(() => {
        if (window.innerWidth < 1200) {
            setToggleUp(true);
            setToggleWidth(false);
        }
        else {
            setToggleUp(false);
            setToggleWidth(false);
        }
    }, [window.innerWidth]);
    const IconLinks = [IconFonts.facebook, IconFonts.github, IconFonts.linkedin, IconFonts.email];
    return (
        <div className={cx('sidebar', 'shadow', toggleUp ? 'sidebar-hide' : '')}>
            <div
                className={cx(
                    themeMode.textColor,
                    themeMode.bgColor,
                    'btn-sidebar',
                    'd-flex',
                    'd-sm-none',
                    'justify-content-center',
                    'align-items-center',
                    'shadow',
                )}
                onClick={handleSidebar}
            >
                <i className="fa fa-bars fa-large"></i>
            </div>
            <CDBSidebar
                breakpoint={'1200'}
                className={cx(themeMode.textColor, themeMode.bgColor, toggleUp ? 'sidebar-scroll-up' : '', 'fs-6')}
                toggled={toggleWidth}
            >
                <CDBSidebarHeader prefix={<i className="fa fa-bars fa-large"></i>} className={cx('sidebar-header')}>
                    {' '}
                    <a href="/" className={cx('text-decoration-none', themeMode.textColor, 'fs-6')}>
                        Minh Nguyen
                    </a>
                </CDBSidebarHeader>
                <CDBSidebarContent className={cx('sidebar-content')}>
                    <CDBSidebarMenu>
                        {publicRoutes.map((route, index) => (
                            <a href={`#${route.path}`} key={index} className={cx(themeMode.textColor)}>
                                <CDBSidebarMenuItem
                                    className={cx('item-menu', route.path === activeId ? 'item-menu-active' : '')}
                                    icon={route.icon}
                                >
                                    {route.component}
                                </CDBSidebarMenuItem>
                            </a>
                        ))}
                        <CDBSidebarHeader
                            className={cx('d-flex', 'align-items-center', 'justify-content-center')}
                            prefix={
                                <div className={cx('mode-toggle-button')}>
                                    <input
                                        type="checkbox"
                                        id="darkmode-toggle"
                                        checked={isChecked}
                                        onChange={handleToggle}
                                    />
                                    <label for="darkmode-toggle">
                                        <Sun2 classNames={cx('sun')} />
                                        <Moon classNames={cx('moon')} />
                                    </label>
                                </div>
                            }
                        ></CDBSidebarHeader>
                    </CDBSidebarMenu>
                </CDBSidebarContent>
                <CDBSidebarFooter>
                    <div className="d-flex justify-content-center flex-wrap gap-3 mb-3">
                        {IconLinks.map((icon, index) => (
                            <OverlayTrigger key={index} placement="top" overlay={<Tooltip>{icon.name}</Tooltip>}>
                                {
                                    <a
                                        className={cx(themeMode.textDetail, 'icon-link-sidebar')}
                                        target="_blank"
                                        href={icon.link}
                                    >
                                        {icon.icon}
                                    </a>
                                }
                            </OverlayTrigger>
                        ))}
                    </div>
                </CDBSidebarFooter>
            </CDBSidebar>
        </div>
    );
};

export default Sidebar;
