import MasterLayout from '~/components/MasterLayout';
import { useSelector } from 'react-redux';
import { themeClass } from '../../Theme';
import style from './Footer.module.scss';
import CV from '~/assets/files/CV_Minh_Nguyen_Web_Developer.pdf';
import Button from '~/components/Button';

const cx = require('classnames/bind').bind(style);
function Footer() {
    const theme = useSelector((state) => state.theme);
    const themeMode = themeClass[theme];
    return (
        <MasterLayout id="footer" mode={2} className="d-flex flex-sm-row flex-column justify-content-between align-items-center">
            <p className={cx(themeMode.textColor, 'footer-text')}>
                Copyright © 2023 <a href="#home">Minh Nguyen</a>
            </p>
            <Button
                to={CV}
                target="_blank"
                outline
                righticon={<i class="fas fa-cloud-download-alt"></i>}
            >
                Download CV
            </Button>
        </MasterLayout>
    );
}

export default Footer;
