import { useSelector } from 'react-redux';
// import { themeClass } from '../../Theme';
import MasterLayout from '~/components/MasterLayout';
import style from './WhatIDo.module.scss';
import IconBox from './IconBox';
import { IconFonts } from '~/assets/icons';

const cx = require('classnames/bind').bind(style);
function WhatIDo() {
    const theme = useSelector((state) => state.theme);
    // const themeMode = themeClass[theme];

    const whatIDoClass = ['d-flex', 'flex-wrap', 'justify-content-center', 'align-items-center'];
    return (
        <MasterLayout id="what-i-do" mode={2} titlePage="Services" subtitlePage="What I Do">
            <div className={cx(whatIDoClass)}>
                <IconBox
                    whatdo={1}
                    icon={<i className="fas fa-image"></i>}
                    title="Landing page"
                    detail="Design responsive website at the request of cilents, using HTML, CSS, JS, ReactJS."
                    skills={[IconFonts.html, IconFonts.css, IconFonts.js, IconFonts.react]}
                />
                <IconBox
                    whatdo={1}
                    icon={<i className="fas fa-code"></i>}
                    title="Front-end skill"
                    detail="Good use of front-end languages such as HTML, CSS, JS, ReactJS, SASS, Bootstrap, jQuery."
                    skills={[IconFonts.js, IconFonts.react,  IconFonts.sass, IconFonts.bootstrap, IconFonts.jquery]}
                />
                <IconBox
                    whatdo={1}
                    icon={<i className="fas fa-database"></i>}
                    title="Back-end skill"
                    detail="Build database for the projects and use back-end languages such as PHP, NodeJS, Laravel, MySQL."
                    skills={[IconFonts.php, IconFonts.mysql, IconFonts.nodejs, IconFonts.laravel]}
                />
                <IconBox
                    whatdo={1}
                    icon={<i className="fas fa-desktop"></i>}
                    title="Web development"
                    detail="Participate in 2 stages of the projects from design to development."
                />
            </div>
        </MasterLayout>
    );
}

export default WhatIDo;
