import { useSelector } from 'react-redux';
import classNames from 'classnames/bind';
import Card from 'react-bootstrap/Card';
import { TypeAnimation } from 'react-type-animation';

import { themeClass } from '../../Theme';
import styles from './Home.module.scss';
import Button from '~/components/Button';
import Scrollspy from '~/components/Sidebar/useScrollspy';

const cx = classNames.bind(styles);

function Home() {
    const activeId = Scrollspy(['home', 'about', 'what-i-do', 'portfolio', 'contact'], 250);
    const theme = useSelector((state) => state.theme);
    const themeMode = themeClass[theme];
    const homeClass = ['vh-100', 'overflow-auto', 'position-relative', 'mw-100'];
    const cardClass = [
        themeMode.textColor,
        themeMode.home.bgColor,
        'card-home',
        'd-flex',
        'col-12',
        'col-lg-6',
        'rounded-0',
        'mw-100',
    ];
    const cardBodyClass = [
        activeId === 'home' ? 'card-home-content-animation' : '',
        'card-home-content',
        'p-4',
        'col-12',
        'd-flex',
        'flex-column',
        'justify-content-center',
        'align-items-lg-stretch',
        'float-lg-start',
        'align-items-center',
        'text-lg-start',
        'text-center',
    ];
    return (
        <section className={cx(homeClass)} id="home">
            <a className={cx('scoll-down-btn')} href="#about-me">
                <i className="fas fa-chevron-down"></i>
            </a>
            <div className={cx('home-background-image')}></div>
            <div className={cx(themeMode.home.bgBackground, 'home-content', 'd-flex', 'col-12', 'mw-100')}>
                <div
                    className={cx(
                        'card-img-avatar',
                        'rounded-0',
                        'col-6',
                        'd-none',
                        'd-lg-block',
                        'shadow-lg',
                        'positon-relative',
                    )}
                >
                    <div
                        className={cx('position-absolute', 'bottom-0')}
                        style={{ height: '200px', width: '100%' }}
                    ></div>
                </div>
                <Card className={cx(cardClass)}>
                    <Card.Body className={cx(cardBodyClass)}>
                        {activeId ? (
                            <TypeAnimation
                                sequence={['Hello!', 1000, "I'm a Web Developer.", 1000]}
                                wrapper="span"
                                speed={150}
                                className={cx('fs-3', 'text-wrap', 'w-100')}
                                repeat={0}
                            />
                        ) : null}
                        <h1>Nguyen Hoang Khanh Minh</h1>
                        <span className={cx(themeMode.secondaryPage.textColor, 'content-underline')}></span>
                        <div
                            style={{ height: '70px'}}
                            className={cx(
                                'd-flex',
                                'flex-row',
                                'flex-xl-row',
                                'flex-lg-column',
                                'border-0',
                                'justify-content-center',
                                'justify-content-xl-start',
                                'align-items-start',
                            )}
                        >
                            <Button outline to="#projects">
                                Projects
                            </Button>
                            <Button to="#contact">Hire me</Button>
                        </div>
                    </Card.Body>
                </Card>
            </div>
        </section>
    );
}

export default Home;
