import styles from './Button.module.scss';

const cx = require('classnames/bind').bind(styles);
function Button({ children, to, type, outline = false, className="", righticon, ...props }) {
    const aboutMeButtonClass = [
        outline ? 'outline-portfolio-button' : 'portfolio-button-none',
        'portfolio-button',
        'fs-6',
        'shadow',
        'd-flex',
        'justify-content-center',
        'align-items-center',
    ];
    var Comp = 'button';
    if (to) {
        Comp = 'a';
    }
    return (
        <Comp href={to} type={type} className={cx(aboutMeButtonClass, className)} {...props}>
            {children}
            {righticon ? <span>{righticon}</span> : ''}
        </Comp>
    );
}

export default Button;
