import { useState, useRef } from 'react';
import { useSelector } from 'react-redux';
import { themeClass } from '../../Theme';
import MasterLayout from '~/components/MasterLayout';
import classNames from 'classnames/bind';
import styles from './Projects.module.scss';
import { EN } from '../../lan/en';
import Modal from 'react-bootstrap/Modal';

const cx = classNames.bind(styles);
function Projects() {
    const theme = useSelector((state) => state.theme);
    const themeMode = themeClass[theme];

    const [show, setShow] = useState(false);
    const [projectPhoto, setProjectPhoto] = useState('');
    const handleShow = (photo) => () => {
        setProjectPhoto(photo);
        setShow(true);
    };

    return (
        <MasterLayout id="projects" mode={2} titlePage="Projects" subtitlePage="My Projects" classNames={cx('d-flex')}>
            <div className={cx('d-flex', 'flex-wrap')}>
                <Modal show={show} onHide={() => setShow(false)} centered size='xl'>
                    <img src={projectPhoto} className={cx('col-12')} />
                </Modal>
                {EN.projects.projects.map((project) => (
                    <div className={cx('col-12','col-xxl-6', 'px-2', 'mb-4')}>
                        <div
                            className={cx(
                                themeMode.projects.textColor,
                                themeMode.bgColor,
                                'project-card',
                                'card',
                                'p-4',
                                'border-0',
                                'rounded-0',
                                'mx-2',
                            )}
                        >
                            <h4 className={cx('text-center', 'fw-nomal', 'mb-3')}>{project.title}</h4>
                            <div className={cx('d-flex', 'grid', 'gap-3', 'flex-lg-nowrap', 'flex-wrap')}>
                                <div className={cx('project-img', 'col-lg-7','col-12')}>
                                    <img
                                        src={project.photo}
                                        alt={project.title}
                                        className={cx('col-12')}
                                        onClick={handleShow(project.photo)}
                                    />
                                </div>
                                <div className={cx('col-lg-5', 'col-12')}>
                                    <div className={cx('project-info', 'fs-6')}>
                                        <h5>{project.info.title}</h5>
                                        <p>{project.info.content}</p>
                                    </div>
                                    <div className={cx('project-details', 'fs-6')}>
                                        <h5>{project.details.title}</h5>
                                        <ul className={cx(themeMode.borderColor)}>
                                            <li className={cx(themeMode.borderColor)}>
                                                <span>{project.details.client.title}</span>
                                                {project.details.client.content}
                                            </li>
                                            <li className={cx(themeMode.borderColor)}>
                                                <span>{project.details.responsibilities.title}</span>
                                                {project.details.responsibilities.content}
                                            </li>
                                            <li className={cx(themeMode.borderColor)}>
                                                <span>{project.details.technologies.title}</span>
                                                <ul className={cx('project-details-techlist')}>
                                                    {project.details.technologies.content.map((tech) => (
                                                        <li>{tech}</li>
                                                    ))}
                                                </ul>
                                            </li>
                                            <li className={cx(themeMode.borderColor)}>
                                                <span>{project.details.position.title}</span>
                                                {project.details.position.content}
                                            </li>
                                            <li className={cx(themeMode.borderColor)}>
                                                <span>{project.details.date.title}</span>
                                                {project.details.date.content}
                                            </li>
                                            <li className={cx(themeMode.borderColor)}>
                                                <span>{project.details.url.title}</span>
                                                <a                                                 
                                                href={project.details.url.name}>
                                                    {project.details.url.content}
                                                </a>
                                            </li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                ))}
            </div>
        </MasterLayout>
    );
}

export default Projects;
