import { useSelector } from 'react-redux';
import { themeClass } from '~/Theme';
import style from '../WhatIDo.module.scss';
import OverlayTrigger from 'react-bootstrap/OverlayTrigger';
import Tooltip from 'react-bootstrap/Tooltip';

const cx = require('classnames/bind').bind(style);
function IconBox({ icon, title, detail, whatdo, skills }) {
    const theme = useSelector((state) => state.theme);
    const themeMode = themeClass[theme];
    const entering = (e) => {
        // e.children[0].style.borderTopColor = 'green';
        // e.children[1].style.backgroundColor = 'green';
    };

    const whatIDoTitleClass = [themeMode.textColor, 'text-start', 'fs-5'];
    const whatIDoDetailClass = [themeMode.textDetail, 'text-start', 'fs-6'];
    var whatIDoIconClass = [];
    var whatIDoItemClass = [];

    const skillIcons = [
        themeMode.textDetail,
        'skill-icons-animation',
        'd-flex',
        'align-items-center',
        'align-self-center',
        'flex-wrap',
        'gap-3',
    ];
    if (whatdo === 1) {
        whatIDoIconClass = ['what-i-do-icon', 'me-3', themeMode.bgColor, 'rounded-0', 'shadow'];
        whatIDoItemClass = ['what-i-do', 'd-flex', 'mb-5', 'col-lg-6', 'px-5'];
    } else {
        whatIDoIconClass = ['language-icon', themeMode.bgColor, 'rounded-0', 'mb-1'];
        whatIDoItemClass = ['what-i-do', 'd-flex', 'flex-column', 'align-items-center', 'col-md-auto', 'col-3'];
    }
    return (
        <div className={cx(whatIDoItemClass)}>
            <div className={cx(whatIDoIconClass)}>{icon}</div>
            {whatdo === 1 ? (
                <div className={cx('icon')}>
                    <h4 className={cx(whatIDoTitleClass)}>{title}</h4>
                    <p className={cx(whatIDoDetailClass)}>{detail}</p>
                    {!skills == [''] ? (
                        <div className={cx(skillIcons)}>
                            {/* <h6>Skills:</h6> */}
                            {skills.map((skill, index) => (
                                <OverlayTrigger
                                    onEntering={entering}
                                    key={index}
                                    placement={'bottom'}
                                    overlay={<Tooltip>{skill.name}</Tooltip>}
                                >
                                    <span>
                                        <IconBox icon={skill.icon} />
                                    </span>
                                </OverlayTrigger>
                            ))}
                        </div>
                    ) : null}
                </div>
            ) : null}
        </div>
    );
}

export default IconBox;
