import { useSelector, useDispatch } from 'react-redux';
import { alertMessage } from '~/redux/actions';

import style from './Alert.module.scss';
import { useEffect } from 'react';

const cx = require('classnames/bind').bind(style);
function Alert() {
    const alert = useSelector((state) => state.alert);
    const dispatch = useDispatch();
    useEffect(() => {
        setTimeout(() => {
            dispatch(alertMessage({ message: '' }));
        }, 3000);
    }, [alert]);
    if (alert === '') return null;
    return (
        <div className={cx('alert-message', 'd-flex', 'justify-content-center')}>
            <div className={cx('alert', 'alert-success', 'col-12', 'shadow')} role="alert">
                Your message has been sent successfully.<p>Thank you!</p>
            </div>
        </div>
    );
}
export default Alert;
